@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300&display=swap);
.wildLogo {
  width: 400px;
  margin: 20px auto -50px auto;
}

.wildLogo img {
  width: 100%;
}

@media screen and (max-width: 700px) {
  .wildLogo {
    width: 250px;
    margin: 20px auto -30px auto;
  }
}

.homeNews {
  display: block;
  margin: auto auto;
  padding-bottom: 10%;
  background-color: #ffb400;
  width: 75%;
  border-radius: 10px;
  text-align: center;
}

.homeNews h3 {
  font-size: 35px;
  color: var(--main-color);
}

@media only screen and (min-width: 600px) {
  .homeNews {
    display: block;
    padding-bottom: 2%;
    background-color: #ffb400;
    width: 86%;
    border-radius: 10px;
    text-align: center;
  }

  .articleList {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: auto;
    width: auto;
  }
}

.newsArticles {
  margin: 3vh 3vh 1vh 3vh;
  position: relative;
  text-align: center;
  color: black;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 15px #2d4059;
}

.newsArticles > h2 {
  margin: auto;
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
  transition: all 2s ease;
}

.newsArticles:hover > img {
  opacity: 0.2;
  transition: 0.5s;
  transform: scale(1.15);
}

.newsArticles:hover > h2 {
  display: flex;
  transform: scale(1.4);
}

@media only screen and (min-width: 600px) {
  .newsArticles {
    max-width: 80%;
  }

  .newsArticles > img {
    width: 100%;
    transition: all 0.75s ease;
  }

  .newsArticles > h2 {
    transition: all s ease;
  }
}

/* card height is width x 1,5735 */
.cardXL {
  width: 260px;
  height: calc(260px * 1.5735);
  font-size: 100%;
}

.cardL {
  width: 240px;
  height: calc(240px * 1.5735);
  font-size: 95%;
}

.cardM {
  width: 220px;
  height: calc(220px * 1.5735);
  font-size: 90%;
}

.cardS {
  width: 200px;
  height: calc(200px * 1.5735);
  font-size: 85%;
}

.cardXS {
  width: 180px;
  height: calc(180px * 1.5735);
  font-size: 80%;
}

.commonCard {
  border: 5px white solid;
}

.rareCard {
  border: 5px blue solid;
}

.ultraCard {
  border: 5px green solid;
}

.epicCard {
  border: 5px orange solid;
}

.legendaryCard {
  border: 5px purple solid;
}

.gameCard {
  border-radius: 15px;
  overflow: hidden;
  box-sizing: border-box;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.7);
}

.gameCard section {
  width: 100%;
}

.cardVisual {
  height: 70%;
}

.cardVisual img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.playValue {
  height: 15%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-color);
}

.playValue p {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.battleStat {
  width: 25%;
  border-radius: 0 100% 100% 0;
  background-color: rgba(121, 9, 9, 1);
  color: white;
}

.summonStat {
  width: 25%;
  border-radius: 100% 0 0 100%;
  background-color: rgba(16, 121, 9, 1);
  color: white;
}

.rarityStat {
  width: 50%;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rarityStar {
  width: 40%;
}

.charaName {
  height: 15%;
  background-color: var(--main-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.charaName p {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--secondary-color);
  text-align: center;
}

.carouselLayout {
  width: auto;
  margin: 30px auto;
}

.slider-single-content {
  box-shadow: none !important;
}

.react-3d-carousel .slider-container .slider-right div {
  border: none;
}

.react-3d-carousel .slider-container .slider-left div {
  border: none;
}

@media screen and (max-width: 700px) {
  .carouselLayout {
    margin: 30px auto 150px auto;
  }
}

.home {
  min-height: 100vh;
}

@media screen and (max-width: 700px) {
