    width: 70px;
    height: 100vh;
    background-color: var(--secondary-color);

    position: -webkit-sticky;

    position: sticky;
    top: 0;
    transition: 400ms;
    margin-bottom: -300px;

  }

  .menuLogo {
    display: flex;
    justify-content: center;
    color: #000;
    background-color: var(--secondary-color);
    border: 0;
  }

  .menuBars {
    border-top: 2px solid black;
    padding: 0;
    display: flex;
    color: #000;
  }

  .menuBars:hover {
    background-color: black;
    color: var(--secondary-color);
  }

  nav.navMenuActive {
    background-color: var(--secondary-color);
    color: #000;
    font-size: 22px;
    font-weight: bold;
    height: 100vh;
    width: 190px;
    display: flex;
    justify-content: start;
    flex-direction: column;
    padding: 0;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    transition: 600ms;
    margin-bottom: -300px;
  }

  .textShow {
    display: flex;
    align-items: center;
